<template>
  <div>
    <div class="col-12 align-self-center">
      <h1 class="text-center" v-html="html(outputText)"></h1>
        <div class="row">
          <div class="col-12 align-self-center" v-if="show">
            <transition name="fade" class="m-2">
              <a href="https://www.linkedin.com/in/melovinpedida/" target="_blank"><font-awesome-icon :icon="['fab', 'linkedin']" size="2x"/></a>
            </transition>
            <transition name="fade" class="m-2">
              <a href="https://www.facebook.com/melovinpogi" target="_blank"><font-awesome-icon :icon="['fab', 'facebook-square']" size="2x"/></a>
            </transition>
            <transition name="fade" class="m-2">
              <a href="mailto:pedidamelovin@gmail.com?subject=inquiry-github" target="_blank"><font-awesome-icon :icon="['fas', 'envelope-square']" size="2x"/></a>
            </transition>
          </div>
        </div>
    </div>
  </div>
</template>

<script>

const introMsg = `Hello, I'm Melovin Pedida. <br>
                  I'm a Software Developer.`
export default {
  name: 'Index',
  methods: {
    output( ){

      if (this.ctr < this.msg.length) {
        this.outputText += this.msg.charAt(this.ctr);
        this.ctr++
        setTimeout(this.output, 100)
      } else {
        this.fadeButton()
      }
    },
    html(){ 
      return this.outputText;
    },
    fadeButton() { 
      this.show = !this.show;
    },
  },
  mounted(){
    this.output();
  },
  data(){ 
    return {
      msg: introMsg,
      outputText: '',
      ctr: 0,
      show: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #ffffff;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to
/* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



